.Nav { 
height: 70px;
background: linear-gradient(170deg, #1bc059, #0d47a1);
display: flex;
justify-content: space-between;
align-items: center;
position: relative;
}
.title{ 
font-size: 30px;
color: #fff;
font-weight: 600;
font-family: 'Times New Roman', Times, serif;
text-transform: uppercase;
margin-left: 20px ;
}
.cartImg{ 
    height: 48px;
    margin-right: 20px;
}
.CartIconContainer {
position: relative;
cursor: pointer;
}

.CartCount{
    background-color:yellow;
    border-radius: 50%;
    padding: 4px 8px;
    position: absolute;
    right: 10px;
    top: -5px;
    font-size: 12px;}
    
